import React from 'react';
import GalleryItem from './GalleryItem';
import images from "../data.json";
import "./GalleryHorizontal.css";

class GalleryHorizontal extends React.Component {

    constructor(props) {
		super(props);
		this.state = {
			imageList: images.items
		};
	}

	render() {
		return (
			<>
				<h4 className='pt-2'>Responzivn Gallery Horizontal s pouitm CSS Flexbox</h4>

				<div className="b-gal b-gal-horizontal b pb-3">
					<div className="b-gal-w">
						{this.state.imageList.map((image, index) =>
							<GalleryItem key={index}
							type = {image.type}
							small = {image.small}
							medium = {image.medium}
							large = {image.large}
							alt = {image.alt}
							width ={image.width}
							height = {image.height}
							sizes = {image.sizes}
							/>
						)}
					</div>
					<div className="wnd-empty" />
				</div>
			</>
		)
	}
}

export default GalleryHorizontal;
